export default {
  namespaced: true,
  state: {
    isHadHero: false,
    isLoadingHero: false,
    loadingProcessText: "0:start",
  },
  mutations: {
    SET_IS_HAD_HERO: (state, h) => {
      state.isHadHero = h;
    },
    SET_IS_LOADING_HERO: (state, s) => {
      state.isLoadingHero = s;
    },
    SET_LOADING_PROCESS_TEXT: (state, s) => {
      state.loadingProcessText = s;
    },
  },
  actions: {
    async search({ rootGetters, rootState, dispatch, commit }, { owner }) {
      commit("SET_IS_LOADING_HERO", true);
      commit("SET_LOADING_PROCESS_TEXT", `1-0:开始获取战士tokenId列表[0]`);
      await dispatch("role_warrior/getHero", { owner }, { root: true });
      commit(
        "SET_LOADING_PROCESS_TEXT",
        `2-1:获得战士tokenId列表[${rootGetters.balanceOfWarrior}]`
      );
      await dispatch("role_robber/getHero", { owner }, { root: true });
      commit(
        "SET_LOADING_PROCESS_TEXT",
        `2-2:获得盗贼tokenId列表[${rootGetters.tbalanceOfRobber}]`
      );
      await dispatch("role_mage/getHero", { owner }, { root: true });
      commit(
        "SET_LOADING_PROCESS_TEXT",
        `2-3:获得法师tokenId列表[${rootGetters.balanceOfMage}]`
      );
      await dispatch("role_yx/getHero", { owner }, { root: true });
      commit(
        "SET_LOADING_PROCESS_TEXT",
        `2-4:获得游侠tokenId列表[${rootGetters.balanceOfYx}]`
      );
      // query ming info
      await dispatch("mining/getMingingInfoAll", { owner }, { root: true });
      commit(
        "SET_LOADING_PROCESS_TEXT",
        `2-5:获得打工中角色tokenId列表old[${rootState.mining.balanceOf}],new[${rootState.mining.balanceOf_new}]`
      );

      if (
        rootGetters.balanceOfWarrior == 0 &&
        rootGetters.tbalanceOfRobber == 0 &&
        rootGetters.balanceOfMage == 0 &&
        rootGetters.balanceOfYx == 0 &&
        rootState.mining.balanceOf == 0 &&
        rootState.mining.balanceOf_new == 0
      ) {
        commit("SET_IS_HAD_HERO", false);
        commit("SET_IS_LOADING_HERO", false);
        return;
      }
      commit("SET_IS_HAD_HERO", true);
      await dispatch("role_detail/getRoleDetail", {}, { root: true });
      commit("SET_LOADING_PROCESS_TEXT", `3-0:sucess`);
      commit("SET_IS_LOADING_HERO", false);
    },
  },
};
