<template>
  <div class="text_left search_box">
    <div
      v-loading="isLoadingHero"
      element-loading-background="rgba(0, 0, 0, 0.8)"
    >
      <el-input
        v-model="owner"
        placeholder="owner address"
        clearable
        prefix-icon="el-icon-search"
        :autosize="{ minRows: 1, maxRows: 2 }"
        style="width:370px"
        @change="search"
      />
    </div>
    <div class="process-text" v-if="isLoadingHero">
      {{ loadingProcessText }}
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import { ElMessage } from "element-plus";
export default {
  data: function() {
    return {
      owner: "",
      // loadingProcessText_: "",
    };
  },
  computed: {
    ...mapGetters(["isHadHero", "isLoadingHero", "loadingProcessText"]),
  },
  watch: {
    // loadingProcessText: function(val) {
    //   // console.log("loadingProcessText=", val);
    //   this.loadingProcessText_ = val;
    // },
    isHadHero: function(val) {
      //   console.log("isHadHero val=", val, val_lod);
      if (!val) {
        // message: owner no hero
        ElMessage.warning(`${this.owner} no hero`);
        this.owner = "";
      }
    },
  },
  methods: {
    search: async function() {
      //   console.log("search  owner=", this.owner);
      if (!this.owner || this.owner.trim() == "") {
        return;
      }
      if (this.isSeaching) return;
      await this.$store.dispatch("hero/search", { owner: this.owner });
      this.owner = "";
      ElMessage.success("success");
    },
  },
};
</script>

<style scoped>
.text_left {
  text-align: left;
}
.search_box {
  padding-top: 30px;
  padding-left: 30px;
}
.process-text {
  text-align: center;
  z-index: 9999;
  color: blue;
}
</style>
